import React from 'react'
import { Card, Box, Container } from 'theme-ui'
import { Layout, Stack, Hero, Main, PreFooter, Sidebar } from '@layout'
import CardList from '@components/CardList'
import Divider from '@components/Divider'
import Navigation from '@components/Navigation'
import Pagination from '@components/Pagination'
import NewsletterExpanded from '@widgets/NewsletterExpanded'
import Seo from '@widgets/Seo'
import Categories from '@widgets/Categories'
import Sticky from '@components/Sticky'
import BannerHorizontal from '@widgets/BannerHorizontal'
import BannerVertical from '@widgets/BannerVertical'
import attachSocialIcons from '@helpers/attachSocialIcons'
import useSiteMetadata from '@helpers-blog/useSiteMetadata'
import { useBlogTags, useBlogCategories } from '@helpers-blog'

const styles = {
  heroThumbsContainer: {
    left: `45%`,
    top: `50%`,
    position: `absolute`,
    transform: `translate(-50%, -50%)`,
    display: [`none`, null, null, `block`]
  },
  heroThumbsInner: {
    width: `1/3`,
    ml: `auto`
  }
}

// TODO: <Sidebar>s are hidden for narrow screens, refactor the below
//       so the most recent posts are in the <Main>

const ThreePosts = ({posts, category}) => {
  const group = posts.group.filter((p) => p.categoryName === category)[0]

  return (
              <Stack
                title={group.categoryName}
                titleLink={group.nodes[0].category.slug}
                direction={['column', 'column', 'column', 'row']}
              >
                <Sidebar
                  sx={{
                    pl: 0,
                    pr: [0, null, null, 3],
                    display: [null, `flex`],
                    flexDirection: [`column`, null, null, `row`]
                  }}
                >
                  <CardList
                    nodes={group.nodes}
                    limit={1}
                    columns={[1]}
                    variant={[
                      'horizontal-md',
                      'horizontal',
                      'horizontal',
                      'vertical'
                    ]}
                    omitCategory
                  />
                </Sidebar>
                <Main
                  sx={{
                    display: [null, `flex`],
                    flexDirection: [`column`, null, null, `row`]
                  }}
                >
                  <Divider space={2} />
                  <CardList
                    nodes={group.nodes}
                    limit={1}
                    skip={1}
                    columns={[1]}
                    variant={[
                      'horizontal-md',
                      'horizontal',
                      'horizontal',
                      'vertical'
                    ]}
                    omitCategory
                  />
                  <Divider space={2} />
                </Main>
                <Sidebar
                  sx={{
                    pl: [0, null, null, 3],
                    display: [null, `flex`],
                    flexDirection: [`column`, null, null, `row`]
                  }}
                >
                  <CardList
                    nodes={group.nodes}
                    limit={1}
                    skip={2}
                    columns={[1]}
                    variant={[
                      'horizontal-md',
                      'horizontal',
                      'horizontal',
                      'vertical'
                    ]}
                    omitCategory
                  />
                </Sidebar>
              </Stack>
  )
}


const FivePosts = ({posts, category}) => {
  const group = posts.group.filter((p) => p.categoryName === category)[0]

  return (
              <Stack
                title={group.categoryName}
                titleLink={group.nodes[0].category.slug}
                direction={['column', 'column', 'column', 'row']}
              >
                <Sidebar
                  sx={{
                    pl: 0,
                    pr: [0, null, null, 3],
                    display: [null, `flex`],
                    flexDirection: [`column`, null, null, `row`]
                  }}
                >
                  <CardList
                    nodes={group.nodes}
                    limit={1}
                    columns={[1]}
                    variant={[
                      'horizontal-md',
                      'horizontal',
                      'horizontal',
                      'vertical'
                    ]}
                    omitCategory
                  />
                </Sidebar>
                <Main
                  sx={{
                    display: [null, `flex`],
                    flexDirection: [`column`, null, null, `row`]
                  }}
                >
                  <Divider space={2} />
                  <CardList
                    nodes={group.nodes}
                    limit={3}
                    skip={1}
                    columns={[1, 1, 3, 1]}
                    variant={[
                      'horizontal-md',
                      'horizontal-md',
                      'horizontal-aside'
                    ]}
                    omitCategory
                  />
                  <Divider space={2} />
                </Main>
                <Sidebar
                  sx={{
                    pl: [0, null, null, 3],
                    display: [null, `flex`],
                    flexDirection: [`column`, null, null, `row`]
                  }}
                >
                  <CardList
                    nodes={group.nodes}
                    limit={1}
                    skip={4}
                    columns={[1]}
                    variant={[
                      'horizontal-md',
                      'horizontal',
                      'horizontal',
                      'vertical'
                    ]}
                    omitCategory
                  />
                </Sidebar>
              </Stack>

  )
}


const Posts = ({
  data: { posts = {}, featuredPosts = {}, paginatedPosts = {}, recentPosts = {} },
  ...props
}) => {
  const { pageContext: { services = {}, basePath } = {} } = props
  const { social } = useSiteMetadata()
  const tags = useBlogTags()
  const categories = useBlogCategories()
  const sliderRef = React.useRef()

  console.log(tags)

  console.log(posts)
  const catPosts = {
    "Announcements": posts.group.filter((p) => p.categoryName === 'Announcements')[0],
    "Articles": posts.group.filter((p) => p.categoryName === "Articles")[0],
    "Data Releases": posts.group.filter((p) => p.categoryName === "Data Releases")[0],
    "How Tos": posts.group.filter((p) => p.categoryName === "How Tos")[0],
    "Software Releases": posts.group.filter((p) => p.categoryName === "Software Releases")[0],
  }
  console.log(catPosts)
  console.log(catPosts.Articles)

  return (
    <Layout {...props}>
      <Seo title='Home' />

      <Stack effectProps={{ effect: false }}>
          <Hero />
      </Stack>


      <Stack effectProps={{ effect: false }}>
        <Main>
          <Divider space={2} />
          <CardList
            nodes={recentPosts.nodes}
            limit={6}
            columns={[1, 2]}
            variant='horizontal-aside'
            loading='eager'
          />
        </Main>
        <Sidebar sx={{ pl: `3`, flexBasis: `1/4` }}>
          <BannerVertical />
        </Sidebar>
      </Stack>

      <Divider space={4} />


      <Stack effectProps={{ effect: false }}>
      <Main>

      <ThreePosts posts={posts} category="Articles" />
      <Divider space={2} />
      <FivePosts posts={posts} category="How Tos" />
      <Divider space={2} />
      <ThreePosts posts={posts} category="Software Releases" />
      <Divider space={2} />
      <FivePosts posts={posts} category="Data Releases" />

      </Main>
        <Sidebar>
          <Sticky>
          <Categories categories={categories} title=""/>
          <Divider space={1} />
          <Divider space={1} />
          <Divider space={1} />
          <CardList
            nodes={featuredPosts.nodes}
            limit={6}
            columns={[1, 1, 1]}
            variant='horizontal-aside'
            loading='eager'
          />
          <Divider space={1} />
            <Navigation items={attachSocialIcons(social)} iconOnly />
          </Sticky>
        </Sidebar>
      </Stack> 
      
      <Divider space={2} />

      <PreFooter></PreFooter>
      <Stack>
        <Main sx={{ zIndex: 1 }}>
          {services.mailchimp && <NewsletterExpanded simple />}
        </Main>
      </Stack>
    </Layout>
  )
}

export default Posts
